import { useProgress } from "@devowl-wp/real-queue";
import { useCallback } from "react";
import { useStores } from "../store";
import { SCAN_QUEUE_JOB } from "../types";

/**
 * Returns the status / progress of the scanner.
 */
function useScannerProgress(fetchStatusInterval, fetchAdditionalData = false, onCancel) {
  const {
    scannerStore
  } = useStores();
  return useProgress({
    type: SCAN_QUEUE_JOB,
    fetchStatusInterval,
    fetchAdditionalData,
    onAdditionalData: useCallback(({
      "rcb-scan-list": rcbScanList
    }) => {
      const {
        presets,
        externalUrls
      } = rcbScanList;
      scannerStore.resultPresetsFromResponse(presets);
      scannerStore.resultExternalUrlsFromResponse(externalUrls);
    }, [scannerStore]),
    onCancel
  });
}

export { useScannerProgress };